import React, { Component } from "react";
import
{
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Graf from "./Graf";
import Tv from "./Tv";
import Net from "./Net";
import TehMap from "./TehMap";


import "./index.css";
import wrench from "./wrench.png";

class Main extends Component
{
  render()
  {
    return(
      <HashRouter>
        <div>
          <div className="header">
            <img className="header-image" src={wrench} />
            <h1 className="header-title">Техник</h1>
          </div>
          
          <div className="menu">
            <NavLink exact to="/"><button className="buttons">Графици</button></NavLink>
            <NavLink to="/tv"><button className="buttons">Телевизия</button></NavLink>
            <NavLink to="/net"><button className="buttons">Интернет</button></NavLink>
            <NavLink to="/tehmap"><button className="buttons">Карта</button></NavLink>
          </div>
          <div className="article">
            <Route exact path="/" component={Graf}/>
            <Route path="/tv" component={Tv}/>
            <Route path="/net" component={Net}/>
            <Route path="/tehmap" component={TehMap}/>
          </div>
        </div>
      </HashRouter>
    );
  }
}

export default Main;



