import React, { Component } from "react";

var tv = {
  samsung: ["Германия", "Унгария"],
  philips: ["Словения", "Румъния"],
}

class Tv extends Component
{
  render()
  {
    return(
      <div>
        <p>Samsung: {tv.samsung}</p>
        <p>Philips: {tv.philips}</p>
      </div>
    );
  }
}

export default Tv;




